#podrska {
  .nav-link {
    transition: 150ms ease-out;
    padding: 1rem 1rem 1rem 1.5rem;
  }
  .nav-link.active {
    background-color: $gray-100;
    padding: 1rem 1rem 1rem 1.5rem;
    border-radius: 4px;
    border: 1px solid #e0edfa;
    transition: 250ms ease-in;
  }

  .card-collapse__btn {
    padding: 1rem 1.5rem 1rem 1rem;
  }

  .card-collapse__header {
    background-color: $gray-100;
  }

  .card-header {
    border: 0;
  }
}